/**/
.hex-loader {
    margin: auto;
    padding: 10vh;
    display: block;
    height: 43.301%;
    width: 50%;
}


@media only screen and (min-width: 768px) {
    .hex-loader {
        height: 12.999999%;
        width: 15%;
    }
}

.hex-loader__tl {
    -webkit-animation-name: zoom-tl;
            animation-name: zoom-tl;
}
.hex-loader__tr {
    -webkit-animation-name: zoom-tr;
            animation-name: zoom-tr;
}
.hex-loader__l {
    -webkit-animation-name: zoom-l;
            animation-name: zoom-l;
}
.hex-loader__c {
    -webkit-animation-name: zoom-c;
            animation-name: zoom-c;
}
.hex-loader__r {
    -webkit-animation-name: zoom-r;
            animation-name: zoom-r;
}
.hex-loader__bl {
    -webkit-animation-name: zoom-bl;
            animation-name: zoom-bl;
}
.hex-loader__br {
    -webkit-animation-name: zoom-br;
            animation-name: zoom-br;
}

/**/
.hex-loader__tl,
.hex-loader__tr,
.hex-loader__l,
.hex-loader__c,
.hex-loader__r,
.hex-loader__bl,
.hex-loader__br {
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-animation-duration: 4000ms;
            animation-duration: 4000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes zoom-tl {
    0% {
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
    }
    10%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@keyframes zoom-tl {
    0% {
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
    }
    10%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@-webkit-keyframes zoom-tr {
    0%, 10% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    20%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@keyframes zoom-tr {
    0%, 10% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    20%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@-webkit-keyframes zoom-r {
    0%, 20% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    30%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@keyframes zoom-r {
    0%, 20% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    30%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@-webkit-keyframes zoom-br {
    0%, 30% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    40%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@keyframes zoom-br {
    0%, 30% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    40%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
}

@-webkit-keyframes zoom-bl {
    0%, 40% {
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    50%, 90% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    100% {
        -webkit-transform: scale(0,0);
